@import "https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@300;400;500;600;700&family=Montserrat:wght@400;700&display=swap";
.container {
  padding-inline: 20px;
}

@media screen and (width >= 768px) {
  .container {
    padding-inline: 34px;
  }
}

@media screen and (width >= 1280px) {
  .container {
    max-width: 1280px;
    margin-inline: auto;
    padding-inline: 55px;
  }
}

html {
  scroll-behavior: smooth;
}

h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

* {
  border: 0;
  margin: 0;
  padding: 0;
}

p, a {
  font-family: IBM Plex Sans, serif;
  font-size: 14px;
  font-weight: 400;
}

a {
  color: #0f0e08;
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  color: #0f0e08;
  font-family: Montserrat, serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 100%;
}

.title {
  color: #0f0e08;
  text-align: center;
  font-size: 36px;
  font-weight: 700;
}

@media screen and (width >= 768px) {
  .title {
    text-align: left;
    font-size: 48px;
  }
}

.logo {
  text-transform: uppercase;
  color: #0f0e08;
  text-align: center;
  font-family: Montserrat, serif;
  font-size: 30px;
  font-weight: 700;
  line-height: 100%;
  position: relative;
}

.logo:after {
  content: "";
  transform-origin: center;
  background: #0f0e08;
  border-radius: 4px;
  width: 100%;
  height: 2px;
  transition: transform .3s;
  display: block;
  position: absolute;
  bottom: -8px;
  transform: scale(0);
}

.logo:hover:after {
  transform: scale(1);
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.page {
  color: #0f0e08;
  background-color: #fff;
  min-width: 320px;
  transition: all .3s;
  position: relative;
}

.page:after {
  content: "";
  z-index: 10;
  opacity: 0;
  visibility: hidden;
  background-color: #00000080;
  width: 100vw;
  height: 100vh;
  transition: opacity .3s, visibility;
  position: fixed;
  top: 0;
  left: 0;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page:has(.page__menu:target):after {
  opacity: 1;
  visibility: visible;
}

.page__menu {
  pointer-events: none;
  opacity: 0;
  z-index: 20;
  background-color: #1a5a4c;
  width: 100%;
  padding-block: 27px;
  transition: top .3s, opacity .3s;
  position: absolute;
  top: -100%;
  left: 0;
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  top: 0;
}

.menu__content {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

@media screen and (width >= 768px) {
  .menu__content {
    flex-direction: row;
    justify-content: center;
    gap: 38px;
  }
}

@media screen and (width >= 1280px) {
  .menu__content {
    gap: 102px;
  }
}

.menu__top {
  align-items: flex-start;
  gap: 28px;
  display: flex;
}

@media screen and (width >= 768px) {
  .menu__top {
    gap: 38px;
  }
}

@media screen and (width >= 1280px) {
  .menu__top {
    gap: 102px;
  }
}

.menu__list {
  grid-template-columns: repeat(2, 1fr);
  gap: 24px 28px;
  padding: 0;
  list-style: none;
  display: grid;
}

@media screen and (width >= 768px) {
  .menu__list {
    column-gap: 20px;
    min-width: 220px;
  }
}

@media screen and (width >= 1280px) {
  .menu__list {
    min-width: 368px;
  }
}

.menu__link {
  color: #fff;
  font-weight: 500;
  position: relative;
}

.menu__link:after {
  content: "";
  transform-origin: center;
  background: #fff;
  border-radius: 4px;
  width: 100%;
  height: 2px;
  transition: transform .3s;
  display: block;
  position: absolute;
  bottom: -8px;
  transform: scale(0);
}

.menu__link:hover:after {
  transform: scale(1);
}

.menu__line {
  background-color: #fff;
  width: 100%;
  height: 1px;
}

@media screen and (width >= 768px) {
  .menu__line {
    width: 1px;
    height: 145px;
  }
}

.menu__bottom {
  flex-direction: column;
  gap: 16px;
  display: flex;
}

@media screen and (width >= 768px) {
  .menu__bottom {
    flex-direction: row;
    gap: 26px;
  }

  .menu__bottom-text {
    align-self: flex-end;
    width: 144px;
    min-width: 144px;
  }
}

@media screen and (width >= 1280px) {
  .menu__bottom-text {
    align-self: flex-end;
    width: 170px;
    min-width: 170px;
  }
}

.menu__schedule {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

@media screen and (width >= 768px) {
  .menu__schedule {
    width: 170px;
    min-width: 170px;
  }
}

.menu__schedule-list {
  list-style-type: none;
}

.menu__text {
  color: #fff;
  line-height: 150%;
}

.menu__text span {
  font-weight: 600;
}

.icon {
  width: 24px;
  height: 24px;
  display: block;
}

.icon--menu {
  background-image: url("icon-menu.17f3ebeb.svg");
}

.icon--close {
  background-image: url("icon-menu-close.c07524ca.svg");
}

.header {
  box-sizing: border-box;
  color: #0f0e08;
  min-width: 320px;
  max-width: 1440px;
  position: relative;
}

@media screen and (width >= 1280px) {
  .header {
    margin-inline: auto;
  }
}

.header__content {
  --colums: 2;
  --column-gap: 20px;
  column-gap: var(--column-gap);
  grid-template-columns: repeat(var(--colums), 1fr);
  display: grid;
}

@media screen and (width >= 768px) {
  .header__content {
    --colums: 6;
    --column-gap: 30px;
  }
}

@media screen and (width >= 1280px) {
  .header__content {
    --colums: 12;
    min-height: 655px;
  }
}

.header__icon-menu {
  z-index: 1;
  position: absolute;
  top: 27px;
}

@media screen and (width >= 768px) {
  .header__icon-menu {
    top: 39px;
  }
}

.header__top {
  grid-column: 1 / -1;
  min-height: 300px;
  margin-inline: -20px;
  position: relative;
}

@media screen and (width >= 768px) {
  .header__top {
    min-height: 384px;
    margin: 0;
    position: absolute;
    top: 0;
    left: 50%;
    right: 0;
  }
}

@media screen and (width >= 1280px) {
  .header__top {
    min-height: 655px;
  }
}

.header__logo {
  z-index: 2;
  position: absolute;
  top: 9px;
  left: 132px;
}

@media screen and (width >= 768px) {
  .header__logo {
    font-size: 36px;
    top: 15px;
    left: 75px;
  }
}

@media screen and (width >= 1280px) {
  .header__logo {
    left: 100px;
  }
}

.header__img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
}

.header__bottom {
  box-sizing: border-box;
  text-align: center;
  flex-direction: column;
  grid-area: 2 / 1 / auto / -1;
  align-items: center;
  gap: 20px;
  height: 100%;
  padding-top: 20px;
  display: flex;
}

@media screen and (width >= 768px) {
  .header__bottom {
    text-align: left;
    grid-area: 1 / 1 / auto / 4;
    align-items: flex-start;
    gap: 38px;
    padding-top: 127px;
  }
}

@media screen and (width >= 1280px) {
  .header__bottom {
    grid-column: 1 / 6;
    justify-content: space-between;
    padding-top: 206px;
    padding-bottom: 0;
  }
}

.header__bottom--events {
  text-transform: lowercase;
  color: #687480;
  transform-origin: 0 0;
  font-weight: 700;
  line-height: 100%;
  display: none;
  transform: rotate(-90deg)translateX(-14px);
}

.header__bottom--events:before {
  content: "";
  background-color: #1a5a4c;
  width: 70px;
  height: 1px;
  display: block;
}

@media screen and (width >= 1280px) {
  .header__bottom--events {
    align-items: center;
    gap: 20px;
    display: flex;
  }
}

.header__title {
  color: #000;
  text-transform: uppercase;
  font-family: Montserrat, serif;
  font-size: 45px;
  font-weight: 700;
  line-height: 100%;
}

@media screen and (width >= 768px) {
  .header__title {
    font-size: 54px;
    line-height: 120%;
  }
}

@media screen and (width >= 1280px) {
  .header__title {
    font-size: 72px;
  }
}

.header__button {
  box-sizing: border-box;
  color: #0f0e08;
  text-transform: uppercase;
  cursor: pointer;
  background: none;
  border: 1px solid #1a5a4c;
  width: 100%;
  height: 50px;
  padding-left: 100px;
  font-size: 16px;
  font-weight: 500;
  line-height: 50px;
  transition: all .3s;
  display: flex;
  position: relative;
}

.header__button:after {
  content: "";
  background-color: #1a5a4c;
  background-image: url("Vector.61d3448a.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 50px;
  height: 50px;
  transition: all .3s;
  display: block;
  position: absolute;
  right: 0;
}

@media screen and (width >= 768px) {
  .header__button:after {
    width: 70px;
    height: 70px;
  }
}

.header__button:hover {
  border: 1px solid #3c7f6f;
}

.header__button:hover:after {
  background-color: #3c7f6f;
}

@media screen and (width >= 768px) {
  .header__button {
    max-width: 270px;
    height: 70px;
    padding-left: 68px;
    line-height: 70px;
  }
}

@media screen and (width >= 1280px) {
  .header__button {
    align-self: flex-end;
    width: 60%;
    max-width: 100%;
  }
}

.main {
  row-gap: 100px;
  padding-top: 100px;
  display: grid;
  position: relative;
}

@media screen and (width >= 768px) {
  .main {
    row-gap: 120px;
    padding-top: 120px;
  }
}

.current-events {
  grid-column: 1 / -1;
}

.current-events__content {
  --colums: 2;
  --column-gap: 20px;
  column-gap: var(--column-gap);
  grid-template-columns: repeat(var(--colums), 1fr);
  row-gap: 40px;
  display: grid;
}

@media screen and (width >= 768px) {
  .current-events__content {
    --colums: 6;
    --column-gap: 30px;
  }
}

@media screen and (width >= 1280px) {
  .current-events__content {
    --colums: 12;
  }
}

@media screen and (width >= 768px) {
  .current-events__content {
    row-gap: 48px;
  }
}

@media screen and (width >= 1280px) {
  .current-events__content {
    row-gap: 56px;
  }
}

.current-events__title {
  overflow-wrap: break-word;
  word-spacing: 100vw;
  grid-column: 1 / -1;
}

@media screen and (width >= 768px) {
  .current-events__title {
    word-spacing: normal;
    grid-column: 1 / -1;
  }
}

.current-events__events {
  grid-column: 1 / -1;
  row-gap: 60px;
}

.event {
  --colums: 2;
  --column-gap: 20px;
  column-gap: var(--column-gap);
  grid-template-columns: repeat(var(--colums), 1fr);
  margin-bottom: 60px;
  display: grid;
}

@media screen and (width >= 768px) {
  .event {
    --colums: 6;
    --column-gap: 30px;
  }
}

@media screen and (width >= 1280px) {
  .event {
    --colums: 12;
  }
}

.event:last-child {
  margin-bottom: 0;
}

.event__img {
  grid-column: 1 / -1;
  width: 100%;
  margin-bottom: 20px;
  transition: transform .3s;
}

@media screen and (width >= 768px) {
  .event__img {
    margin-bottom: 40px;
  }
}

@media screen and (width >= 1280px) {
  .event__img {
    grid-column: 1 / 8;
    margin: 0;
  }
}

.event__img:hover {
  transform: scale(1.03);
}

.event__info {
  grid-column: 1 / -1;
}

@media screen and (width >= 768px) {
  .event__info {
    grid-column: 1 / 5;
  }
}

@media screen and (width >= 1280px) {
  .event__info {
    grid-column: 9 / -1;
    align-self: center;
  }
}

.event__date {
  gap: 54px;
  margin-bottom: 15px;
  display: flex;
}

@media screen and (width >= 768px) {
  .event__date {
    grid-column: 1 / 4;
    margin-bottom: 30px;
  }
}

@media screen and (width >= 1280px) {
  .event__date {
    grid-column: 9 / 12;
    margin-bottom: 30px;
  }
}

.event__date--name {
  text-transform: uppercase;
  color: #687480;
  font-size: 16px;
  font-weight: 500;
  line-height: 150%;
}

.event__date--time {
  color: #1a5a4c;
  font-size: 16px;
  font-weight: 700;
  line-height: 150%;
}

.event__title {
  margin-bottom: 15px;
  font-size: 28px;
