}

@media screen and (width >= 768px) {
  .lecture__title {
    font-size: 36px;
  }
}

.gallery__content {
  flex-direction: column;
  display: flex;
  overflow: hidden;
}

.gallery__title {
  grid-column: 1 / -1;
  margin-bottom: 40px;
}

.gallery__images {
  grid-template-columns: repeat(4, 100%);
  grid-auto-flow: column;
  gap: 20px;
  margin-bottom: 20px;
  transition: all .3s;
  display: grid;
}

@media screen and (width >= 768px) {
  .gallery__images {
    grid-template-columns: repeat(4, calc(50% - 15px));
    gap: 30px;
  }
}

@media screen and (width >= 1280px) {
  .gallery__images {
    grid-template-rows: 600px 360px;
    grid-template-columns: repeat(3, 1fr);
  }
}

.gallery__img {
