  }

  .gallery__img--2 {
    grid-column: 2 / -1;
  }

  .gallery__img--3 {
    grid-column: 1 / 3;
  }

  .gallery__img--4 {
    grid-column: 3;
  }
}

.gallery__img:hover {
  transform: scale(1.04);
}

.gallery__dotts {
  gap: 22px;
  margin-inline: auto;
