.footer__content {
  --colums: 2;
  --column-gap: 20px;
  column-gap: var(--column-gap);
  grid-template-columns: repeat(var(--colums), 1fr);
  padding-block: 50px;
  display: grid;
}

@media screen and (width >= 768px) {
  .footer__content {
    --colums: 6;
    --column-gap: 30px;
